<template>
  <div class="my-container">
    <v-dialog
      v-model="showConfigPathVariableDialog"
      overlay-opacity="0.30"
      max-width="1080"
      @click:outside="showConfigPathVariableDialog = !showConfigPathVariableDialog"
    >
      <key-value-config @hideDialog="showConfigPathVariableDialog = false" />
    </v-dialog>

    <v-dialog
      persistent
      v-model="showWindowSelectorConfig"
      overlay-opacity="0.30"
      max-width="1080"
      @click:outside="hideWindowSelectorConfig"
    >
      <window-selector-config/>
    </v-dialog>

    <v-card min-height="570" width="790" elevation="5" class="action-config">
      <v-card-title>
        <v-row>
          <v-col cols="8">
            <v-select
              class="action-select"
              :items="actionTypes"
              v-model="currKey().type"
              outlined
              @change="clearValue"
              :menu-props="{ maxHeight: 900 }"
              :disabled="disableSelectBox"
            ></v-select>
          </v-col>
          <v-col cols="4">

            <v-select
              outlined
              class="action-select"
              :menu-props="{ maxHeight: 900 }"
              :disabled="disableSelectBox"
              :items="windowSelectors"
              item-text="key"
              item-value="id"
              v-model="currentWindowSelector"
              @change="changeWindowSelector"
            ></v-select>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-text>
        <template v-if="currKey().type === ''">
          <v-text-field
            autocomplete="off"
            label=" ()"
            v-model="currKey().toActivate"
            @input="activateOrRun"
          ></v-text-field>
          <v-text-field
            autocomplete="off"
            label=":  /  / URL"
            v-model="currKey().toRun"
            @input="activateOrRun"
          ></v-text-field>
          <v-text-field
            autocomplete="off"
            label=""
            v-model="currKey().cmdArgs"
            @input="activateOrRun"
          ></v-text-field>
          <v-text-field
            autocomplete="off"
            label=""
            v-model="currKey().workingDir"
            @input="activateOrRun"
          ></v-text-field>
          <v-card-actions>
            <v-btn class="action-button" color="purple" dark outlined @click="execute('bin/WindowSpy.ahk')"
              > </v-btn
            >
            <v-btn class="action-button" color="purple" dark outlined target="_blank" href="/ProgramPathExample.html"
              > </v-btn
            >
            <v-btn class="action-button" color="purple" dark outlined @click="configPathVariable"
              ></v-btn
            >
          </v-card-actions>
          <br />
          <pre class="tips">

 Tips: (1) ,  ,  
       (2)  Shift ,    ()
       (3)  URL  https://google.comms-settings:display</pre
          >
        </template>

        <template v-if="currKey().type === ''">
          <v-textarea
            auto-grow
            rows="1"
            label=""
            v-model="currKey().keysToSend"
            @input="sendKeys"
          ></v-textarea>
          <!-- <img alt="img" :src="require('../assets/send-keys.png')" /><img /> -->
          <pre class="tips">

 Tips: (1) <a target="_blank" href="SendKeyExample.html" style="color: green;"></a>
       (2)  abc ,   abc 
       (3)  abc ,   {text} ,  {text}abc</pre>
        </template>

        <template v-if="currKey().type === ' ahk '">
          <v-text-field
            autocomplete="off"
            label=" ( data/custom_functions.ahk)"
            v-model="currKey().value"
          ></v-text-field>
        </template>

        <template v-if="currKey().type === ''">
          <v-radio-group v-model="currKey().label" @change="mouseActionChanged">
            <v-row>
              <v-col>
                <v-radio
                  v-for="action in mouseActions"
                  :key="action.label"
                  :label="`${action.label}`"
                  :value="action.label"
                ></v-radio>
              </v-col>
              <v-col>
                <v-radio
                  v-for="action in scrollActions"
                  :key="action.label"
                  :label="`${action.label}`"
                  :value="action.label"
                ></v-radio>
              </v-col>
            </v-row>

            <br />
            <v-divider></v-divider>
            <br />

            <v-row>
              <v-col>
                <v-radio
                  v-for="action in clickActions"
                  :key="action.label"
                  :label="`${action.label}`"
                  :value="action.label"
                ></v-radio>
              </v-col>
              <v-col> </v-col>
            </v-row>
          </v-radio-group>
        </template>

        <template v-if="currKey().type === ''">
          <v-radio-group v-model="currKey().value">
            <v-row>
              <v-col>
                <v-radio
                  v-for="action in windowActions1"
                  :key="action.label"
                  :label="`${action.label}`"
                  :value="action.value"
                ></v-radio>
              </v-col>
              <v-col>
                <v-radio
                  v-for="action in windowActions2"
                  :key="action.label"
                  :label="`${action.label}`"
                  :value="action.value"
                ></v-radio>
              </v-col>
            </v-row>

            <br />
            <v-divider></v-divider>
            <br />

            <v-row>
              <v-col>
                <v-radio
                  v-for="action in windowActions3"
                  :key="action.label"
                  :label="`${action.label}`"
                  :value="action.value()"
                ></v-radio>
              </v-col>
              <v-col> </v-col>
            </v-row>
          </v-radio-group>
        </template>

        <template v-if="currKey().type === ''">
          <v-radio-group v-model="currKey().value">
            <v-row>
              <v-col>
                <v-radio
                  v-for="action in otherFeatures1"
                  :key="action.label"
                  :label="`${action.label}`"
                  :value="action.value"
                ></v-radio>
              </v-col>
              <v-col>
                <v-radio
                  v-for="action in otherFeatures2"
                  :key="action.label"
                  :label="`${action.label}`"
                  :value="action.value"
                ></v-radio>
              </v-col>
            </v-row>

            <br />
            <v-divider></v-divider>
            <br />
            <v-row>
              <v-col>
                <v-radio
                  v-for="action in otherFeatures3"
                  :key="action.label"
                  :label="`${action.label}`"
                  :value="action.value"
                ></v-radio>
              </v-col>
              <!-- <v-col>
                <v-radio
                  v-for="action in otherFeatures2"
                  :key="action.label"
                  :label="`${action.label}`"
                  :value="action.value"
                ></v-radio>
              </v-col> -->
            </v-row>

            <!-- <v-row>
              <v-col>
                <v-radio
                  v-for="action in clickActions"
                  :key="action.label"
                  :label="`${action.label}`"
                  :value="action.value"
                ></v-radio>
              </v-col>
              <v-col> </v-col>
            </v-row> -->
          </v-radio-group>
        </template>
        <template v-if="currKey().type === ''">
          <v-radio-group v-model="currKey().value">
            <v-row>
              <v-col>
                <v-radio
                  v-for="action in textFeatures1"
                  :key="action.label"
                  :label="`${action.label}`"
                  :value="action.value"
                ></v-radio>
              </v-col>
              <v-col>
                <v-radio
                  v-for="action in textFeatures2"
                  :key="action.label"
                  :label="`${action.label}`"
                  :value="action.value"
                ></v-radio>
              </v-col>
            </v-row>

            <br />
            <v-divider></v-divider>
            <br />

            <!-- <v-row>
              <v-col>
                <v-radio
                  v-for="action in clickActions"
                  :key="action.label"
                  :label="`${action.label}`"
                  :value="action.value"
                ></v-radio>
              </v-col>
              <v-col> </v-col>
            </v-row> -->
          </v-radio-group>
        </template>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import { bindWindow, escapeFuncString, executeScript, mapKeysToSend, notBlank } from '../util.js'
import { host, EMPTY_KEY } from '../util'
import _ from 'lodash'
import KeyValueConfig from './KeyValueConfig.vue'
import WindowSelectorConfig from './WindowSelectorConfig.vue'

export default {
  components: { KeyValueConfig, WindowSelectorConfig },
  created() {},
  props: {
    currentKey: { type: String },
  },
  watch: {
    currentKey(newValue, oldValue) {
      this.currentWindowSelector = '2'
    }
  },
  data() {
    return {
      currentWindowSelector: '2',
      showConfigPathVariableDialog: false,
      showWindowSelectorConfig: false,
      mouseActions: [
        { label: '', value: '' },
        { label: '', value: '' },
        { label: '', value: '' },
        { label: '', value: '' },
      ],
      scrollActions: [
        { label: '', value: '' },
        { label: '', value: '' },
        { label: '', value: '' },
        { label: '', value: '' },
      ],
      clickActions: [
        { label: '', value: '' },
        { label: '', value: '' },
        { label: '', value: '' },
        { label: '', value: '' },
        { label: '', value: '' },
      ],
      windowActions1: [
        { label: '', value: 'SmartCloseWindow()' },
        { label: '', value: 'send !{tab}' },
        { label: '', value: 'SwitchWindows()' },
        {
          label: '(EDSFX)',
          value: 'send ^!{tab}',
        },
        {
          label: '',
          value: 'send {LControl down}{LWin down}{Left}{LWin up}{LControl up}',
        },
        {
          label: '',
          value: 'send {LControl down}{LWin down}{Right}{LWin up}{LControl up}',
        },
        { label: '', value: 'send #+{right}' },
      ],
      windowActions2: [
        { label: '', value: 'winmaximize, A' },
        { label: '', value: 'winMinimizeIgnoreDesktop()' },
        {
          label: '(1200x800)',
          value: 'center_window_to_current_monitor(1200, 800)',
        },
        {
          label: '(1370x930)',
          value: 'center_window_to_current_monitor(1370, 930)',
        },
        { label: '', value: 'ToggleTopMost()' },
        { label: ' (Alt+Esc)', value: 'send !{Esc}' },
        { label: ' (Shift+Alt+Esc)', value: 'send +!{Esc}' },
      ],
      windowActions3: [
        { label: '  ', value: () => bindWindow(this.$route.name, this.currentKey) },
      ],
      otherFeatures1: [
        {
          label: '',
          value: '\nsleep 300\nDllCall("LockWorkStation")\nreturn',
        },
        {
          label: '',
          value: 'DllCall("PowrProf\\SetSuspendState", "Int", 0, "Int", 0, "Int", 0)',
        },
        { label: '', value: 'slideToShutdown()' },
        { label: '', value: 'slideToReboot()' },
        { label: '', value: 'run, bin\\ahk.exe bin\\soundControl.ahk' },
        {
          label: '',
          value: 'run, bin\\ahk.exe bin\\changeBrightness.ahk',
        },
        { label: ' MyKeymap ', value: 'openSettings()' },
      ],
      otherFeatures2: [
        { label: ' MyKeymap', value: 'quit(false)' },
        { label: 'MyKeymap', value: 'run, %A_WorkingDir%' },
        {
          label: '  ',
          value: 'run, shell:RecycleBinFolder',
        },
        { label: '  ', value: 'run, shell:downloads' },
        { label: '  ', value: 'run, shell:my pictures' },
        { label: '  ', value: 'run, shell:My Video' },
        { label: '  ', value: 'run, shell:Personal' },
      ],
      otherFeatures3: [
        { label: '', value: 'restartExplorer()' },
        { label: ' CapsLock ', value: 'toggleCapslock()' },
        { label: '  ', value: 'toggleAutoHideTaskBar()' },
      ],
      textFeatures1: [
        { label: '', value: 'setColor("#D05")' },
        { label: '', value: 'setColor("#b309bb")' },
        { label: '', value: 'setColor("#FF00FF")' },
        { label: '', value: 'setColor("#2E66FF")' },
        { label: '', value: 'setColor("#080")' },
      ],
      textFeatures2: [],
    }
  },
  methods: {
    execute(arg) {
      executeScript(arg)
    },
    configPathVariable() {
      this.showConfigPathVariableDialog = true
    },
    hideWindowSelectorConfig() {
      this.showWindowSelectorConfig = false
      this.currentWindowSelector = '2'
      this.changeWindowSelector('2')
    },
    sendKeys() {
      this.currKey().prefix = '*'
      const result = ['']
      const keysToSend = this.currKey().keysToSend
      if (keysToSend) {
        const lines = keysToSend
          .split('\n')
          .filter(x => x && _.trim(x).length > 0)
          .map(x => mapKeysToSend(x))
        result.push(lines.join('\n'))
      }
      result.push('return')
      this.currKey().value = result.join('\n')
    },
    activateOrRun() {
      const toActivate = escapeFuncString(this.currKey().toActivate)
      let toRun = escapeFuncString(this.currKey().toRun)
      let cmdArgs = escapeFuncString(this.currKey().cmdArgs)
      let workingDir = escapeFuncString(this.currKey().workingDir)
      // console.log(toActivate, toRun)

      if (!toActivate) {
        this.currKey().toActivate = ''
      }

      // 

      for (const item of this.$store.state.config.pathVariables) {
        if (item.key && item.value) {
          const re = new RegExp(`%${item.key}%`, 'g')
          if (toRun) {
            toRun = toRun.replace(re, item.value)
          }
          if (cmdArgs) {
            cmdArgs = cmdArgs.replace(re, item.value)
          }
          if (workingDir) {
            workingDir = workingDir.replace(re, item.value)
          }
        }
      }

      this.currKey().value = !(notBlank(toRun) || notBlank(toActivate))
        ? ''
        : `
    path = ${toRun}
    workingDir = ${workingDir}
    ActivateOrRun("${toActivate}", path, "${cmdArgs}", workingDir)
    return`
    },
    clearValue() {
      //  value ,   'value'  'type' 
      this.currKey().value = ''
      for (const key of Object.keys(this.currKey())) {
        if (!['type', 'value'].includes(key)) {
          delete this.currKey()[key]
        }
      }
      // console.log(Object.entries(this.currKey()))
    },
    changeWindowSelector(new_value) {
      if (new_value === '1') {
        this.showWindowSelectorConfig = true
        return
      }
    },
    mouseActionChanged(newValue) {
      console.log('mouseActionChanged')
      let map = {}
      map[''] = ``
      let key = this.currentKey

      map[''] = `scrollWheel("${key}", 1)`
      map[''] = `scrollWheel("${key}", 2)`
      map[''] = `scrollWheel("${key}", 3)`
      map[''] = `scrollWheel("${key}", 4)`

      map[''] = `fastMoveMouse("${key}", 0, -1)`
      map[''] = `fastMoveMouse("${key}", 0, 1)`
      map[''] = `fastMoveMouse("${key}", -1, 0)`
      map[''] = `fastMoveMouse("${key}", 1, 0)`

      map[''] = `leftClick()`
      map[''] = `rightClick()`
      map[''] = `lbuttonDown()`
      map[''] = `centerMouse()`
      map[''] = `moveCurrentWindow()`

      this.currKey().prefix = '*'
      this.currKey().value = map[newValue] || ''
    },
  },
  computed: {
    windowSelectors() {
      const config = this.$store.state.config
      const selectors = [
        {id: '1', key: ' ', value: 'USELESS' },
        {id: '2', key: ' ', value: 'USELESS' },
      ]

      if (config && config.windowSelectors) {
        return [...selectors, ...config.windowSelectors]
      }

      return selectors
    },
    actionTypes() {
      const result = [
        { text: ' ', value: '' },
        { text: ' ', value: '' },
        { text: ' ', value: '' },
        { text: '  ', value: '' },
        { text: ' ', value: '' },
        { text: ' ', value: '' },
        { text: ' ', value: '' },
        { text: '  ahk ', value: ' ahk ' },
      ]
      if (this.$route.name !== 'Capslock') {
        result.splice(3, 1)
      }
      return result
    },
    disableSelectBox() {
      return this.currentKey === EMPTY_KEY
    },
  },
}
</script>

<style>
/*  scoped  css  vuetify  */
label.v-label.v-label--active {
  top: 2px;
  font-size: 1.15em;
  color: darkmagenta;
}
div.v-radio label.v-label {
  color: black;
}
div.v-radio.v-item--active label.v-label {
  color: red;
  font-size: 1.1em;
}
#single-line-code-hint {
  margin-top: -20px;
  color: orangered;
}
.tips {
  margin: 10px;
  color: black;
}
.action-select .v-select__selection {
  /* color: black; */
  /* font-size: 1.1em; */
}
.action-config .v-text-field {
  margin-left: 10px;
  margin-right: 10px;
}
.action-button {
  margin-right: 17px;
}
</style>
